@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #CFE2F3;
  color: #212121;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.LoginForm-input:focus {
    border-color: #D5A6BD !important;
    box-shadow: 0 0 0 0.2rem rgba(213, 166, 189, 0.5) !important;
}

.LoginForm-button {
    background-color: #D5A6BD;
    border-color: #D5A6BD;
    color: #212121;
}

.LoginForm-button:hover,
.LoginForm-button:active, 
.LoginForm-button:focus {
    color: #212121;
    background-color: #c88ca9 !important;
    border-color: #c88ca9 !important;
}
.PasswordReset-input:focus {
    border-color: #D5A6BD !important;
    box-shadow: 0 0 0 0.2rem rgba(213, 166, 189, 0.5) !important;
}

.PasswordReset-button {
    background-color: #D5A6BD;
    border-color: #D5A6BD;
    color: #212121;
}

.PasswordReset-button:hover,
.PasswordReset-button:active, 
.PasswordReset-button:focus {
    color: #212121;
    background-color: #c88ca9 !important;
    border-color: #c88ca9 !important;
}

.collapse  * {
    margin: 0px 15px;
}

.navbar * {
    color: #D5A6BD;
    font-family: 'Pacifico', cursive;
}
.slide-out-left {
	animation: slide-out-left 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

.slide-out-right {
	animation: slide-out-right 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

/**
 * ----------------------------------------
 * animation slide-out-left
 * ----------------------------------------
 */
  @keyframes slide-out-left {
    0% {
      transform: translateX(0);
      opacity: 1;
    }
    100% {
      transform: translateX(-1000px);
      opacity: 0;
    }
  }
  

/**
 * ----------------------------------------
 * animation slide-out-right
 * ----------------------------------------
 */
  @keyframes slide-out-right {
    0% {
      transform: translateX(0);
      opacity: 1;
    }
    100% {
      transform: translateX(1000px);
      opacity: 0;
    }
  }
  
